.rating_input {
    display: none;
}
.rating_label {
display: block;
width: 150px;
height: 50px;
margin: 0 3px;
background-color: lightgray;
clip-path: polygon(50% 0%, 61% 35%, 98% 35%, 68% 57%, 79% 91%, 50% 70%, 21% 91%, 32% 57%, 2% 35%, 39% 35%);
cursor: pointer;
}

.rating_label:hover,
.rating_label:hover ~ .rating_label,
.rating_input:checked ~ .rating_label {
  background-color: yellow;
}
